.head-top {
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.barnav {
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px 10px 10px;
  position: sticky;
  top: 0;
  z-index: 999;
  height: 70px;
  width: 100%;
}
.barnav h2 {
  font-size: 30px;
  color: white;
  text-align: center;
}
.barnav h4 {
  font-size: 20px;
  color: white;
  text-align: center;
}
.SearchBar {
  margin: 10px 5px;
  display: flex;
  height: 40px;
  width: max-content;
  border-radius: 20px;
  background-color: rgb(12, 12, 12);
}
.SearchBar ion-icon {
  display: flex;
  font-size: 30px;
  height: 40px;
  width: 30px;
  margin: 0px 10px;
  color: grey;
  border-radius: 20px 0px 0px 20px;
}
.SearchBar input {
  display: flex;
  height: 40px;
  width: 80vw;
  background-color: rgb(12, 12, 12);
  border: none;
  padding: 0px 10px;
  border-radius: 0px 20px 20px 0px;
  color: aliceblue;
}
