:root {
  --primary-color: #994f09;
  --background-color: #f9f5f0;
  --second-color: #e7e0e0;
  --font-color: #151515;
  --font2-color: #414040;
}
.container {
  max-width: 600px;
}

.price__btn {
  color: var(--font-color);
  background-color: rgba(255, 255, 255, 0.5);
  border: 2px solid grey;
  padding: 2px 15px;
  border-radius: 5px;
  margin-top: 5px;
}
.price__btn1 {
  color: grey;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  padding: 2px 15px;
  border-radius: 5px;
  margin-top: 5px;
}
.formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  justify-content: center;
  height: 60vh;
}
.formulario-titulo {
  color: var(--font-color);
}

.contraolvidada {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contraolvidada button {
  width: max-content;
}
.contraolvidada .contra1 {
  height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.password {
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 55vw;
  border-radius: 10px;
  margin: 5px 5px 10px 5px;
  padding: 0px 10px 0px 0px;
  border: 1px solid grey;
}
.password input {
  padding: 0px 0px 0px 10px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border: none;
  height: 25px;
}
.password ion-icon {
  color: var(--primary-color);
  font-size: 17px;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
form button {
  margin: 5px 5px;
}
.flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mostrarUsers,
.formulario1 {
  min-height: 60vh;
}
